














































































import { Component, Prop, Vue } from 'vue-property-decorator'
import DatePicker from './DatePickers.vue'
import { } from 'vuex'

enum flag {
  Order,
  Ordering,
  Failed,
  Success
}

@Component({
  components: {
    DatePicker
  }
})
export default class BookingDialog extends Vue {
  @Prop() value!: Boolean
  private flag = flag
  private state = flag.Order
  private datePickerIsShow = false
  private datePickerTarget = 'from'
  private selected: Set<string> = new Set()
  private from = ''
  private to = ''
  get title () {
    switch (this.state) {
      case flag.Order:
      case flag.Ordering:
        return '預約時段'
      case flag.Failed:
        return '預約失敗'
      case flag.Success:
        return '預約成功'
    }
  }
  fromOnFocus () {
    this.datePickerTarget = 'from'
    this.datePickerIsShow = true
  }
  toOnFocus () {
    this.datePickerTarget = 'to'
    this.datePickerIsShow = true
  }
  onAdd (value: string) {
    if (this.datePickerTarget === 'from') {
      this.from = value
    } else {
      this.to = value
    }
  }
  onRemove (value: string) {
    if (this.datePickerTarget === 'from') {
      this.from = value
    } else {
      this.to = value
    }
  }
  onCancel () {
    this.state = flag.Failed
  }
  onConfirm () {
    this.state = flag.Success
  }
}
